.navigation {
  float: left;
  width: 30%;
  height: 100%;
  position: fixed;
  overflow: hidden;
}

.viewer {
  padding: 2px;
  float: right;
  width: 69%;
  height: 100%;
}
.special-letter {
  color: purple;
}
::selection {
  color: white;
  background-color: purple;
}

.prof-pic {
  width: 80%;
  height: 80%;
  border-radius: 50%;
  margin: 10px;
}

.link-div {
  color: black;
  font-size: 120%;
  padding-left: 20%;
  font-weight: bold;
  border: 1px solid white;
}
.link-div:hover {
  background-color: purple;
  color: white;
}

a:link {
  text-decoration: none;
}

a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:active {
  text-decoration: none;
}
