.container {
  padding-left: 20px;
  font-weight: 600;
  font-size: 120;
}

.left-box {
  float: left;
  position: relative;
}

.right-box {
  margin-right: 15%;
  float: right;
  position: relative;
}

.card {
  text-align: justify;
  margin: 10px;
  padding: 10px;
  background-color: rgb(230, 230, 230);
}

input[type="text"],
textarea {
  border-radius: 2px;
  padding: 10px;
  margin: 5px;
  color: #000;
  width: 80%;
  font-size: 110%;
  font-family: Arial;
  border: 1px solid black;
}

::selection {
  color: white;
  background-color: purple;
}

.special-letter {
  color: purple;
}

button {
  border-radius: 2px;
  border: 1px solid black;
  padding: 10px;
  margin: 5px;
  color: #000;
  font-size: 110%;
  font-family: Arial;
  background: white;
}

button:hover {
  background: purple;
  border: 1px solid white;
  color: white;
  cursor: pointer;
}
